.task {
    padding: 8px;
    margin: 6px 0;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    min-height: 94px;
    z-index: 1;
    opacity: 1;
    border: 1px solid transparent;
    cursor: text;
    background: #ffffff;
}

.task.light {
    background: #ffffff;
}

.container_toDo.dark .task {
    background-color: #1B2121;
}

.container_inProgress.dark .task {
    background-color: #0B2530;
}

.container_done.dark .task {
    background-color: #0C2C23;
}

.task:first-child {
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
}

.task:last-child {
    border-bottom-right-radius: 8px;
    border-bottom-left-radius: 8px;
}

.task:only-child {
    border-radius: 8px;
}

.task[draggable="true"] {
    cursor: move;
    cursor: grab;
    cursor: -webkit-grab;
    cursor: -moz-grab;
}

.grabbing * {
    cursor: move !important;
    cursor: -webkit-grabbing !important;
    cursor: -moz-grabbing !important;
    cursor: grabbing !important;
}

.taskMoving {
    opacity: 0.4 !important;
    border: 1px dashed #e4cbfa;
}